<template>
  <model>
    <div class="setting">
      <div class="content">
        <div class="heart"></div>
        <div class="set">
          <div class="name item">
            <span class="tips">昵称:</span>
            <input type="text" placeholder="设置专属昵称" />
          </div>
          <div class="phone item">
            <span class="tips">手机号:</span>
            <input type="text" placeholder='输入 "ta" 的手机号' />
          </div>
        </div>
        <div class="btn">
          确 认
        </div>
      </div>
    </div>
  </model>
</template>

<script>
import model from '../base/model'
export default {
  name: 'mysetting',
  components: { model }
}
</script>

<style lang="stylus">
@import '../common/stylus-mixins/mixins.styl'
.setting
  height 100%
  background #f1f1f1
  display flex
  flex-direction column
  align-items center
  .content
    margin-top 2rem
    bg-image('../assets/my-setting/矩形2')
    wh-bg(10.19rem, 12.86rem)
    display flex
    justify-content flex-start
    align-items center
    flex-direction column
    .heart
      margin-top 1.5rem
      wh-bg(1.24rem, 1.05rem)
      bg-image('../assets/my-setting/图层11')
    .btn
      margin-top 2rem
      height 1rem
      border-radius 1rem
      padding .5rem 1.5rem
      border 1px solid #ee534f
      color #ee534f
      line-height 1rem
      font-size 24px
    .set
      padding 2rem 1rem 0 1rem
      display flex
      flex-direction column
      align-items flex-start
      justify-content center
      .item
        margin-bottom 0.3rem
        display flex
        align-items center
        .tips
          color #4a4a4a
          font-size 16px
          margin-right 0.3rem
          flex-shrink 0
        input
          border none
          outline none
          color #e5e5e5
          font-size 18px
          width 80%
          &.active
            border none
        &.name
          input
            font-size 34px
input::-webkit-input-placeholder
  /* WebKit browsers */
  color #e5e5e5
input::-moz-placeholder
  /* Mozilla Firefox 19+ */
  color #e5e5e5
input:-ms-input-placeholder
  /* Internet Explorer 10+ */
  color #e5e5e5
</style>


