<template>

  <div class="home">
    <div class="i-love">
      <div class="content">
        <span class="i-love-you">我喜欢你</span>
        <span class="name">李泽言</span>
        <span class="change" @click="$router.push({name:'mysetting'})"></span>
      </div>
      <div class="tips">点击隐藏</div>
    </div>
    <div class="who-love-me">
      <div class="title">
        <div class="heart"></div>
        <span class="text">谁在暗恋我</span>
      </div>
      <div class="loved-count">
        已有<span class="count">5</span>位将你设置为暗恋对象噢~
      </div>
      <span class="use-tips">如若对方也喜欢你，将摈除一切备胎，暗恋成功！</span>
      <div class="wrapper" ref="wrapper">
        <div class="love-your" ref="love">
          <div class="item" ref="item">
            <div class="avatar mm"></div>
            <span class="name">某某小管家</span>
            <span class="des">暗恋了你</span>
          </div>
          <div class="item" ref="item">
            <div class="avatar"></div>
            <span class="name">某某</span>
            <span class="des">暗恋了你</span>
          </div>
          <div class="item" ref="item">
            <div class="avatar"></div>
            <span class="name">某某</span>
            <span class="des">暗恋了你</span>
          </div>
          <div class="item" ref="item">
            <div class="avatar"></div>
            <span class="name">某某</span>
            <span class="des">暗恋了你</span>
          </div>
          <div class="item" ref="item">
            <div class="avatar"></div>
            <span class="name">某某</span>
            <span class="des">暗恋了你</span>
          </div>
          <div class="item" ref="item">
            <div class="avatar"></div>
            <span class="name">某某</span>
            <span class="des">暗恋了你</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'home',
  mounted() {
    this._initScroll()
  },
  activated(){
    this._initScroll()
  },
  methods: {
    _initScroll() {
      this.$nextTick(() => {
        let RootRem = document.querySelector('html').style.fontSize
        let width = (this.$refs.item.offsetWidth + 33) * 6
        this.$refs.love.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.wrapper, {
              scrollX: true,
              scrollY: false,
              startX: 0,
              click: true
            })
          } else {
            this.scroll.refresh()
          }
        })
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../common/stylus-mixins/mixins.styl'
.who-love-me
  height calc(100% - 11.27rem)
  padding 0.8rem
  .title
    display flex
    justify-content flex-start
    align-items center
    .heart
      wh-bg(0.7rem, 0.6rem)
      bg-image('../assets/me/图层11')
    .text
      font-size 14px
      margin-left 0.1rem
      color #4a4a4a
  .loved-count
    font-size 18px
    color #4a4a4a
    margin-top 0.4rem
    .count
      color #ee534f
      font-size 26px
      margin 0 0.3rem
  .use-tips
    font-size 12px
    color #888
  .wrapper
    width 100%
    overflow hidden
    .love-your
      padding-top 0.64rem
      display flex
      flex-direction row
      justify-content flex-start
      align-items center
      .item
        display flex
        flex-direction column
        align-items center
        justify-content center
        margin-right 0.27rem
        .avatar.mm
          bg-image('../assets/me/首页')
          wh-bg(1.8rem, 1.8rem)
        .avatar
          bg-image('../assets/me/图层12')
          wh-bg(1.8rem, 1.8rem)
        .name
          margin-top 0.2rem
          font-size 14px
          color #888888
        .des
          margin-top 0.2rem
          font-size 18px
          color #4a4a4a
.i-love
  height 8.47rem
  background-size 100% 8.47rem
  bg-image('../assets/me/圆角矩形4')
  position relative
  padding-bottom 0.8rem
  border-bottom 1px solid #c9c9c9
  background-repeat no-repeat
  .tips
    position absolute
    top 6.2rem
    font-size 12px
    left 50%
    transform translate(-50%)
    color #fff
  .content
    position absolute
    color #fff
    top 2rem
    display flex
    justify-content center
    align-items center
    flex-direction column
    left 50%
    transform translate(-50%)
    .i-love-you
      font-size 24px
      font-weight 700
    .name
      font-size 16px
      margin-top 0.2rem
    .change
      wh-bg(0.48rem, 0.48rem)
      margin-top 0.3rem
      bg-image('../assets/me/编辑')
</style>




