<template>
  <div class="nav">
    <div class="me route" @click="onSelectRoute(0)" :class="{active:activeIndex === 0}">
      <div class="btn" :class="{active:activeIndex === 0}"></div>
      <span class="text">我</span>
    </div>
    <div class="diary route" @click="onSelectRoute(1)" :class="{active:activeIndex === 1}">
      <div class="btn" :class="{active:activeIndex === 1}"></div>
      <span class="text">暗恋日记</span>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      activeIndex: 0
    }
  },
  methods:{
    onSelectRoute(i){
      if(i === 0){
        this.$router.push({name:'home'})
        this.activeIndex = 0
      }
      if(i === 1){
        this.activeIndex = 1
        this.$router.push({name:'diary'})
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../common/stylus-mixins/mixins.styl'
.nav
  display flex
  justify-content space-around
  align-items center
  border-top 1px solid #dfdfdf
  background #fff
  .route
    display flex
    align-items center
    justify-content center
    flex-direction column
    .text
      margin-top 0.1rem
      font-size 14px
      color #c7c7c7
    &.active
      .text
        color #4a4a4a
    &.me
      .btn
        wh-bg(.7rem,.66rem)
        bg-image('../../assets/navgation/图层15')
        &.active
          bg-image('../../assets/navgation/图层6')
    &.diary
      .btn
        wh-bg(.87rem,.66rem)
        bg-image('../../assets/navgation/图层9')
        &.active
          bg-image('../../assets/navgation/图层7拷贝')
</style>



