import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Diary from './views/Diary.vue'
import MySetting from './views/MySetting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/diary',
      name: 'diary',
      component: Diary
    },
    {
      path: '/mySetting',
      name: 'mysetting',
      component:MySetting
    }
  ]
})



