<template>
  <div id="app">
    <keep-alive>
    <router-view id="views"/>
    </keep-alive>
    <navigation class="nav"/>
  </div>
</template>

<script>

import Navigation from './components/navigation/navigation'

export default{
  components:{Navigation}
}

</script>

<style lang="stylus">
html
  font-family "Arial","Microsoft YaHei","黑体","宋体",sans-serif
body
  padding 0
  margin 0
#app
  height 100vh
#views
  height calc(100vh - 1.5rem)
.nav
  position fixed
  bottom 0
  height 1.5rem
  width 100%
</style>



